.notification-container{background-color:white;color:rgba(0,0,0,0.65);padding:10px;display:flex;flex-direction:row;border-bottom:solid 1px #e8e8e8}.notification-container a{color:rgba(0,0,0,0.65);font-weight:bold}.notification-container-body{margin-left:10px;display:flex;flex-direction:column;flex:1}.notification-container:hover{background-color:rgba(196,196,196,0.33)}.notification-unseen{background-color:rgba(230,230,230,0.33)}.notification-content-time{color:rgba(141,141,141,0.78);padding-top:10px}